export default {
	data() {
		return {
			title: 'Hello',
			user:null
		}
	},
	onLoad() {
		// this.user = JSON.parse(uni.getStorageInfoSync('user'));
		this.getUserData()
		// getUserInfo1
		// let _this = this;
		// uni.getStorage({
		// 	key:"code",
		// 	success:function(res){
		// 		_this.user = res.data;
		// 		console.log(res);
		// 	}
		// })
		
	},
	methods: {
		myaddress(){
			uni.navigateTo({
				"url":"/pages/mine/address"
			})
		},
		getUserData(){
			let _this = this;
			this.$ajax("/userInfo","POST",{}).then(res=>{
				if(res.vip){
					_this.user = res
				}
				
				console.log(res);
			})
			// let user = uni.getStorageInfoSync("user")
			
			// this.user = user;
			
		},
		bill17(){
			uni.navigateTo({
				"url":"/pages/shop/bill"
			})
		},
		getUserInfo1(){
			let _this = this
			uni.getUserInfo({
					provider:"weixin",
					success:function(res){
						_this.$ajax('/wxRegister','POST',{encryptedData:res.encryptedData,iv:res.iv}).then((registerRes)=>{
							  if(registerRes){
								 uni.setStorageSync('user',JSON.stringify(res));
								 _this.user = res
								 // uni.reLaunch({
								 // 	"url":"/pages/mine/mine"
								 // })
							  }else{
								  uni.showToast({
								  	"title":"获取用户信息失败，请重新点击",
								  	"icon":"none"
								  })
							  }															  
						})
						
					}	
					
				})
			
		},
		toUrl(url){
			uni.navigateTo({
				"url":url
			})
		},
		login(){
			this.$login();
		}
	}
}
